.about {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.about .left {
  text-align: center;
  margin: auto;
  padding: 1rem;
  max-width: 700px;
  /* background-color: pink; */
}

.about .left p {
  margin: 1.2rem 0;
  font-size: medium;
}
.about .right {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  border-radius: 300px;
  /* background-color: blue; */
  border-color: purple;
}

.right .img-container {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
  width: 250px;
  height: 250px;
  border-radius: 300px;
  /* background-color: purple; */
  border-color: purple;
}

.right .img {
  max-width: 100%; /* Ensure the image doesn't overflow */
  border: 3px solid #333;
  border-radius: 300px;
  border-color: white;
}

.right .top {
  grid-column: 1 / span 8;
  grid-row: 1;
  /* padding-top: 20%; */
  z-index: 6;
}

.right .bottom {
  grid-column: 4/-1;
  grid-row: 1;
  z-index: 6;
}

@media screen and (max-width: 768px) {
  .about {
    flex-direction: column; /* Stack items vertically on small screens */
    padding-right: 2rem;
  }

  .right {
    max-width: 100%; /* Full width for right column on small screens */
  }

  .left {
    margin-bottom: 1rem; /* Add some spacing between the left and right sections */
  }
}
